import { Container, Row } from "reactstrap";
import { useParams } from "react-router-dom";
import { selectFarmById } from "../features/farms/farmsSlice";
import FarmDetail from "../features/farms/FarmDetail";
import CommentsList from "../features/comments/CommentsList";
import SubHeader from "../components/SubHeader";
import { useSelector } from "react-redux";

const FarmDetailPage = () => {
  const { farmId } = useParams();
  const farm = useSelector(selectFarmById(farmId));

  return (
    <Container>
      <SubHeader current={farm.name} detail={true} />
      <Row>
        <FarmDetail farm={farm} />
        <CommentsList farmId={farmId} />
      </Row>
    </Container>
  );
};

export default FarmDetailPage;
